.portrait-wrapper {
  display: contents;
}

.portrait {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  margin: 20px;
  width: 200px;
  max-width: calc(100% - 20px - 20px);
  text-decoration: none;
}

.portrait[data-style="small"] {
  width: 100px;
}

.portrait[data-style="tiny"] {
  flex-direction: row;
  gap: 15px;
  width: unset;
  text-align: left;
}

.portrait-image {
  width: 100%;
  aspect-ratio: 1 / 1;
  border-radius: 999px;
  object-fit: cover;
  box-shadow: var(--shadow);
}

.portrait[data-style="tiny"] .portrait-image {
  width: 50px;
}

.portrait[data-style="tiny"] .portrait-role {
  display: none;
}

.portrait-text {
  display: flex;
  flex-direction: column;
  font-weight: 200;
  color: var(--text);
  gap: 4px;
  line-height: 1.2; 
}

.portrait-name {
  font-weight: 600;        /* clearly bolder */
  color: var(--text);         
}

.portrait-role .icon {
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(20px + 10%);
  aspect-ratio: 1 / 1;
  border-radius: 999px;
  background: var(--background);
  box-shadow: var(--shadow);
  transform: translate(14%, 14%);
}

.portrait[data-style="small"] .portrait-role .icon {
  left: -2px;
  top: -2px;
}

/* Smooth hover interaction for member portraits */
.portrait {
  transition: transform 260ms ease, box-shadow 260ms ease;
}

.portrait-image {
  transition: transform 260ms ease;
}

/* Hover / focus effect */
.portrait:hover .portrait-image,
.portrait:focus-within .portrait-image {
  transform: scale(1.05);   /* zoom in slightly */
}

.portrait:hover,
.portrait:focus-within {
  transform: translateY(-4px);  /* subtle lift */
}
